
.navbar-nav {
    position: relative;
    -webkit-justify-content: baseline;
            justify-content: baseline;
    color: lightgray;
}

.navbar-nav li {
    margin: 10px 20px;
}

.container {
    display: -webkit-flex;
    display: flex;
    min-height: 80%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
}
.clickable-image {
    width: 250px;
    height: 250px;
    float: left;
    margin: 8px 25px;
}

.clickable-image img {
    width: 100%;
    height: auto;
}
.footer {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: #007bff;
    overflow: auto;
    color: white;
}
